@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;800&display=swap);
:root {
  --first-color: #f9f9fc;
  --second-color: #e2e2e2;
  --white: #fff;
  --black: #1a1a1a;
  --line: #cbd3d9;
  --gray: #34495e;
  --blue :#37a4fd;

}

.global-title{
  font-size: 60px;
    font-weight: 800;
}

.first-btn {
  padding: 1rem;
  background-color: transparent;
  
  font-size: 20px;
  width: 11rem;
  height: 4rem;
  border-radius: 0.25rem;
  border: 1px solid #1a1a1a;
  border: 1px solid var(--black);
  cursor: pointer;
  transition: 0.3s ease-out;
}
.first-btn:hover{
  background-color: #1a1a1a;
  background-color: var(--black);
  color: #fff;
  color: var(--white);
  
}

.second-btn {
  padding: 1rem;
  background-color: transparent;
  
  font-size: 20px;
  width: 11rem;
  height: 4rem;
  border-radius: 0.25rem;
  border:none;
  cursor: pointer;
  transition: 0.3s ease-out;
}
.second-btn:hover{
  font-size: 22px;
} 

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;

}
.sidebar{
  width: 24%;
  height: 100vh;
  background-color: var(--white);
  position: fixed;
  transition: all 0.3s ease-out;
}
.main-content{
  width: 76%;
  min-height: 100vh;
  /* background-color: black; */
  margin-left: 24%;

}

.nav-icon{
  display: none;

}

@media screen and (max-width:960px) {
  .sidebar{
  
      transform: translateX(-500%);
      
  }
  .nav-toggle{
    transform: translateX(0);
    width: 60%;
    
  }
  .main-content{
    width: 100%;
    margin-left: 0;
  
  }
  .nav-icon{
    position: fixed;
    z-index: 10;
    right: 4%;
    top:2%;
    display: block;
    cursor: pointer;

  }
  .nav-icon .fas{
      font-size: 40px;
  }
}


